<!--
 * @Description: 分销员按分组查业绩
 * @Author: your name
 * @Date: 2019-12-30 
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div>
    <!-- 筛选条件 -->
    <el-card class="filter-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="按分组查看：">
            <el-select v-model="list.groupId"
                       clearable
                       placeholder="默认选择全部">
              <el-option v-for="item in groupList"
                         :key="item.id"
                         :label="item.name"
                         :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单时间：">
            <el-date-picker v-model="list.registerTime"
                            type="daterange"
                            value-format="yyyy-MM-dd"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container"
             shadow="never">
      <el-collapse accordion>
        <el-collapse-item>
          <template slot="title">
            <i style="font-size:16px;color:#E6A23C"
               class="header-icon el-icon-info"></i><span style="font-size:16px;color:#E6A23C;margin-left:10px">业绩说明</span>
          </template>
          <el-alert title="1、自动结算：系统会自动将佣金结算给分销员，分销员可自行提现。自动结算分为交易完成结算和售后维权处理期结束后再结算。"
                    type="warning"
                    :closable="false"></el-alert>
          <el-alert title="1）交易完成结算：一般情况下交易完成后立即给分销员结算佣金，期间发生的退款会自动扣除  风险提醒：若您选择交易完成结算的方式，则交易完成后发生的维权退款不影响已结算的佣金，可能会造成佣金亏损。"
                    type="warning"
                    :closable="false"></el-alert>
          <el-alert title="2）售后维权处理期结束后再结算：指分销员推广，买家下单后，系统会计算这笔订单在售后维权期（交易完成后的15个自然日）结束后结算佣金。若出现商家余额不足无法结算佣金或系统问题导致结算失败时，订单会仍然存在于待结算中，等待再次发起结算。分销员佣金结算至账户后，即可自助申请提现。"
                    type="warning"
                    :closable="false"></el-alert>
        </el-collapse-item>
      </el-collapse>
    </el-card>

    <div class="table-container">
      <el-table :data="tableList"
                style="width: 100%"
                ref="multipleTable"
                v-loading="loading"
                @sort-change='tableSortChange'
                border>
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column label="分组名称"
                         width="140"
                         align="center">
          <template slot-scope="scope">{{scope.row.groupName}}</template>
        </el-table-column>
        <el-table-column label="创建时间"
                         width="190"
                         prop="createTime"
                         align="center">
        </el-table-column>
        <el-table-column width="180"
                         sortable="custom"
                         prop="orderTotal"
                         align="center"
                         :render-header="renderHeader">
        </el-table-column>
        <el-table-column width="220"
                         sortable="custom"
                         prop="orderAmount"
                         :render-header="renderHeader"
                         align="center">
        </el-table-column>
        <el-table-column label="累计分到佣金(不剔除退款)"
                         prop="commissionAmount"
                         width="240"
                         sortable="custom"
                         align="center">
        </el-table-column>
        <el-table-column label="累计分到邀请奖励"
                         width="200"
                         sortable="custom"
                         prop="inviteAmount"
                         align="center">
        </el-table-column>
        <el-table-column width="120"
                         sortable="custom"
                         prop="groupUserCount"
                         :render-header="renderHeader"
                         align="center">
        </el-table-column>
        <el-table-column label="操作"
                         min-width="200"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       class="button_mian"
                       @click="showDetails(scope.row)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 固定在下面 -->
    <div class="flex-row-end"
         style="margin-top:20px">
      <!-- 页码 -->
      <el-pagination background
                     style="margin-top: 10px;margin-left:20px"
                     @size-change="handleSizeChange"
                     @current-change="pageChange"
                     layout="total,sizes,prev,pager,next,jumper"
                     :page-size="page_size"
                     :page-sizes="[10, 30, 50]"
                     :current-page="pageNum"
                     :total="total">
      </el-pagination>
    </div>

  </div>
</template>
<script>
import {
  initChannelGroupList
} from '@/api/distributionManagement/distributor'
import commonList from '@/utils/commonList'
import { channelGroupList } from '@/api/distributionManagement/performance'
export default {
  props: ["status", "shop", "user"],
  data () {
    return {
      list: {
        groupId: '',
        registerTime: '',
      },
      sort: '',
      sortType: '',
      tableList: [],
      groupList: [],
      pageNum: 1,
      page_size: 10,
      total: 0,
      loading: false,
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick (picker) {
              const end = new Date();
              end.setTime(end.getTime());
              end.setHours(end.getHours());
              end.setMinutes(end.getMinutes());
              end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick (picker) {
              const end = new Date();
              end.setTime(end.getTime() - 3600 * 1000 * 24);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick (picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
    }
  },
  created () {
    // this.searchList()
    this.sort = 'orderAmount'
    this.sortType = 'DESC'
    this.searchGroupList()
  },
  mounted () {

  },
  methods: {
    //自定义table表头
    renderHeader (h, { column, $index }) {
      if ($index === 3) {
        return (
          <el-tooltip class="item"
            effect="dark"
            content="累计成交笔数是指该组分销员转发成功下单和有绑定关系的客户成功下单的订单笔数"
            placement="top">
            <el-button style="color:#909399;font-weight:600"
              type="text"><i class="el-icon-question"></i>总累计成交笔数</el-button>
          </el-tooltip>
        )
      }
      if ($index === 4) {
        return (
          <el-tooltip class="item"
            effect="dark"
            content="累计分销商品交易额是指该组分销员转发成功下单和邀请成功的客户关联的订单实际支付成功的分销商品交易额"
            placement="top">
            <el-button style="color:#909399;font-weight:600"
              type="text"><i class="el-icon-question"></i>总累计分销商品交易额</el-button>
          </el-tooltip>
        )
      }
      if ($index === 7) {
        return (
          <el-tooltip class="item"
            effect="dark"
            content="组成员数：是指该组下所有成员的数量"
            placement="top">
            <el-button style="color:#909399;font-weight:600"
              type="text"><i class="el-icon-question"></i>组成员数</el-button>
          </el-tooltip>
        )
      }
    },

    //查询分销员分组
    searchGroupList () {
      initChannelGroupList({
        shopId: this.shop.shopId
      }).then(res => {
        if (res.code == 200) {
          this.groupList = res.data
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },

    //查询数据列表
    searchList () {
      this.loading = true
      channelGroupList({
        shopId: this.shop.shopId,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        groupId: this.list.groupId,
        sort: this.sort,
        sortType: this.sortType,
        beginTime: this.list.registerTime == null ? '' : this.list.registerTime[0],
        endTime: this.list.registerTime == null ? '' : this.list.registerTime[1],
      }).then(res => {
        this.loading = false
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
          console.log(this.tableList, '=====')
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          });
        }
      })
    },

    //查看分销员单据
    showDetails (row) {
      console.log(row, 111)
      this.$router.push({
        path: '/distributionManagement/performance/groupMember',
        query: {
          id: row.groupId,
          groupName: row.groupName,
          orderTotal: row.orderTotal,
          inviteAmount: row.inviteAmount,
          orderAmount: row.orderAmount,
          userCount: row.userCount,
          commissionAmount: row.commissionAmount
        }
      })
    },

    handleClose () {
      this.dialogFormVisibleTwo = false
      this.form.name = ''
      this.form.orderID = undefined
      this.orderNo = ''
      this.form.month = ''
      this.form.buyTime = ''
    },
    //重置
    reset () {
      this.list = {
        userName: '',
        cellPhone: '',
        orderNo: '',
        registerTime: '',
      }
      this.sort = '',
        this.sortType = ''
      this.pageNum = 1
      this.searchList()
    },

    // 表格数据进行排序
    tableSortChange (column) {
      console.log(column.prop, column.order)
      this.sort = column.prop
      if (column.order == null) {
        this.sortType = ''
        this.searchList()
        return
      }
      if (column.order == "ascending") {
        this.sortType = 'ASC'
      }
      if (column.order == "descending") {
        this.sortType = 'DESC'
      }
      this.searchList()
    },

    //修改每页页数
    handleSizeChange (page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange (page) {
      this.pageNum = page
      this.searchList()
    },
  },
  computed: {
    startTime () {
      return this.status;
    },
  },
  watch: {
    startTime (newValue, oldValue) {
      this.searchList()
      console.log(newValue)
      return newValue;
    },
  }
}
</script>

<style lang="scss" scoped>
.button_mian {
  margin: 5px;
}
.btn-add-export {
  float: right;
  margin-top: -8px;
}
.operate-container {
  margin-top: 10px;
}
.btn-add {
  float: right;
  margin-top: -8px;
}
.store-red {
  margin-left: 70px;
  color: red;
  span {
    display: block;
    margin-bottom: 10px;
  }
}
.table-container {
  margin-top: 10px;
}
</style>
