<!--
 * @Description: 按分销员查询业绩
 * @Author: your name
 * @Date: 2019-12-30 
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div>
    <!-- 筛选条件 -->
    <el-card class="filter-container"
             shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span>筛选搜索</span>
        <el-button style="float: right"
                   @click="searchList"
                   type="primary"
                   size="small">
          查询
        </el-button>
        <el-button style="float: right;margin-right: 15px"
                   @click="reset"
                   size="small">
          重置
        </el-button>
      </div>
      <div style="margin-top: 40px">
        <el-form :inline="true"
                 :model="list"
                 size="small"
                 label-width="140px">
          <el-form-item label="分销员昵称：">
            <el-input v-model="list.userName"
                      clearable
                      placeholder="分销员昵称"></el-input>
          </el-form-item>
          <el-form-item label="分销员手机号：">
            <el-input v-model="list.cellPhone"
                      clearable
                      placeholder="分销员手机号"></el-input>
          </el-form-item>
          <el-form-item label="按分组查看：">
            <el-select v-model="list.groupId"
                       clearable
                       placeholder="默认选择全部">
              <el-option v-for="item in groupList"
                         :key="item.id"
                         :label="item.name"
                         :value="item.id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="订单时间：">
            <el-date-picker v-model="list.registerTime"
                            type="daterange"
                            value-format="yyyy-MM-dd"
                            :picker-options="pickerOptions"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!-- 数据列表 -->
    <el-card class="operate-container"
             shadow="never">
      <i class="el-icon-tickets"></i>
      <span>数据列表</span>
    </el-card>
    <div class="table-container">
      <el-table :data="tableList"
                style="width: 100%"
                ref="multipleTable"
                v-loading="loading"
                @sort-change='tableSortChange'
                border>
        <el-table-column type="index"
                         width="50">
        </el-table-column>
        <el-table-column label="分销员昵称"
                         width="140"
                         align="center">
          <template slot-scope="scope">{{scope.row.userName}}</template>
        </el-table-column>
        <el-table-column label="分销员手机号"
                         width="120"
                         prop="cellPhone"
                         align="center">
        </el-table-column>
        <el-table-column label="所属分组"
                         width="120"
                         prop="groupName"
                         align="center">
        </el-table-column>
        <el-table-column width="180"
                         sortable="custom"
                         prop="orderTotal"
                         align="center"
                         :render-header="renderHeader">
        </el-table-column>
        <el-table-column width="190"
                         sortable="custom"
                         prop="orderAmount"
                         :render-header="renderHeader"
                         align="center">
        </el-table-column>
        <el-table-column label="累计分到佣金(不剔除退款)"
                         prop="commissionAmount"
                         width="250"
                         sortable="custom"
                         align="center">
        </el-table-column>
        <el-table-column label="累计分到邀请奖励"
                         width="180"
                         sortable="custom"
                         prop="inviteAmount"
                         align="center">
        </el-table-column>
        <el-table-column label="加入时间"
                         width="180"
                         sortable="custom"
                         prop="createTime"
                         align="center">
        </el-table-column>
        <el-table-column label="客户数"
                         width="140"
                         sortable="custom"
                         prop="userCount"
                         :render-header="renderHeader"
                         align="center">
        </el-table-column>
        <el-table-column label="操作"
                         min-width="200"
                         align="center">
          <template slot-scope="scope">
            <el-button type="text"
                       class="button_mian"
                       @click="showDetails(scope.row)">查看分销员单据</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="flex-row-end"
           style="margin-top:20px">
        <!-- 页码 -->
        <el-pagination background
                       style="margin-top: 10px;margin-left:20px"
                       @size-change="handleSizeChange"
                       @current-change="pageChange"
                       layout="total,sizes,prev,pager,next,jumper"
                       :page-size="page_size"
                       :page-sizes="[10, 30, 50]"
                       :current-page="pageNum"
                       :total="total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>
<script>
import {
  initChannelGroupList
} from '@/api/distributionManagement/distributor'
import { channelUserList } from '@/api/distributionManagement/performance'
export default {
  props: ["status", "shop", "user"],
  data() {
    return {
      list: {
        userName: '',
        cellPhone: '',
        setStatus: '',
        groupId: '',
        registerTime: '',
      },
      id: '',
      tableList: [],
      groupList: [],
      pageNum: 1,
      page_size: 10,
      total: 0,
      loading: false,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime());
              // end.setHours(end.getHours());
              // end.setMinutes(end.getMinutes());
              // end.setSeconds(end.getSeconds());
              const start = new Date();
              start.setTime(start.getTime());
              // start.setHours(0); start.setMinutes(0); start.setSeconds(0);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '昨天',
            onClick(picker) {
              const end = new Date();
              end.setTime(end.getTime() - 3600 * 1000 * 24);
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
        ]
      },
    }
  },
  created() {
    this.sort = 'orderAmount'
    this.sortType = 'DESC'
    this.searchList()
    this.searchGroupList()
  },
  mounted() {

  },
  methods: {
    //自定义table表头
    renderHeader(h, { column, $index }) {
      if ($index === 4) {
        return (
          <el-tooltip class="item"
            effect="dark"
            content="累计成交笔数是指该分销员转发成功下单和有绑定关系的客户成功下单；"
            placement="top">
            <el-button style="color:#909399;font-weight:600"
              type="text"><i class="el-icon-question"></i>累计成交笔数</el-button>
          </el-tooltip>
        )
      }
      if ($index === 5) {
        return (
          <el-tooltip class="item"
            effect="dark"
            content="累计分销商品交易额是指该分销员转发成功下单和邀请成功的客户关联的订单支付成功的分销商品交易额；"
            placement="top">
            <el-button style="color:#909399;font-weight:600"
              type="text"><i class="el-icon-question"></i>累计分销商品交易额</el-button>
          </el-tooltip>
        )
      }
      if ($index === 9) {
        return (
          <el-tooltip class="item"
            effect="dark"
            content="客户数是指成功邀请并下单的客户数量"
            placement="top">
            <el-button style="color:#909399;font-weight:600"
              type="text"><i class="el-icon-question"></i>交易客户数</el-button>
          </el-tooltip>
        )
      }
    },

    //查询分销员分组
    searchGroupList() {
      initChannelGroupList({
        shopId: this.shop.shopId
      }).then(res => {
        if (res.code == 200) {
          this.groupList = res.data
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          })
        }
      }).catch(err => {
        console.log(err)
      })
    },

    //查询数据列表
    searchList() {
      this.loading = true
      channelUserList({
        shopId: this.shop.shopId,
        pageNum: this.pageNum,
        pageSize: this.page_size,
        userName: this.list.userName,
        cellPhone: this.list.cellPhone,
        groupId: this.list.groupId,
        sort: this.sort,
        sortType: this.sortType,
        beginTime: this.list.registerTime == null ? '' : this.list.registerTime[0],
        endTime: this.list.registerTime == null ? '' : this.list.registerTime[1],
      }).then(res => {
        if (res.code == 200) {
          this.tableList = res.data.list
          this.total = res.data.total
          this.loading = false
          console.log(this.tableList, '=====')
        } else {
          this.$notify.error({
            title: '错误',
            message: res.message
          });
        }
      })
    },
    //重置
    reset() {
      this.list = {
        userName: '',
        cellPhone: '',
        setStatus: '',
        orderNo: '',
        registerTime: '',
        setTime: ''
      }
      this.pageNum = 1
      this.searchList()
    },


    // 表格数据进行排序
    tableSortChange(column) {
      console.log(column.prop, column.order)
      this.sort = column.prop
      if (column.order == null) {
        this.sortType = ''
        this.searchList()
        return
      }
      if (column.order == "ascending") {
        this.sortType = 'ASC'
      }
      if (column.order == "descending") {
        this.sortType = 'DESC'
      }
      this.searchList()
    },

    //查看分销员单据
    showDetails(row) {
      this.$router.push({
        path: '/distributionManagement/performance/documents',
        query: {
          userId: row.userId,
          userName: row.userName,
          cellPhone: row.cellPhone,
          groupName: row.groupName
        }
      })
    },

    //修改每页页数
    handleSizeChange(page) {
      this.page_size = page
      this.searchList()
    },
    //点击跳转页码
    pageChange(page) {
      this.pageNum = page
      this.searchList()
    },
  },
  computed: {
    startTime() {
      return this.status;
    },
  },
  watch: {
    startTime(newValue, oldValue) {
      this.searchList()
      console.log(newValue)
      return newValue;
    },
  }
}
</script>

<style lang="scss" scoped>
.button_mian {
  margin: 5px;
}
.btn-add-export {
  float: right;
  margin-top: -8px;
}
.operate-container {
  margin-top: 10px;
}
.app-container {
  padding: 20px;
}
.btn-add {
  float: right;
  margin-top: -8px;
}
.store-red {
  margin-left: 70px;
  color: red;
  span {
    display: block;
    margin-bottom: 10px;
  }
}
.table-container {
  margin-top: 10px;
}
.fixedB {
  width: 100%;
  padding-right: 300px;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  background: #eee;
  z-index: 100;
  margin-left: -20px;
  .el-pagination {
    display: flex;
    align-items: center;
  }
}
</style>
